import React, { Component } from 'react'
import Paper from 'material-ui/Paper'
import Typography from 'material-ui/Typography'
import { withStyles } from 'material-ui/styles'

const darkBlue = 'rgb(059, 089, 152)';

const resultStyles = theme => ({
    root: theme.mixins.gutters({
        paddingTop: 16,
        paddingBottom: 16,
        marginTop: theme.spacing.unit * 3,
    }),
})

class ResultPanel extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ret: (<h1>The result is not ready</h1>)
        };
    }

    componentWillReceiveProps(nextProps) {
        if ( nextProps.valid === 1 ) {
            this.setState({
                ret: <h1>Valid Results!!!!!!!!</h1>
            });
        } else if ( nextProps.valid === 0 ) {
            this.setState({
                ret: <h1>The result is not ready</h1>
            });
        } else {
            this.setState({
                ret: <h1>Invalid Result.........</h1>
            });
        }
    }

    buildResult() {}

    render() {
        return (
            <div style={{ background: darkBlue }}>
                <Paper className={this.props.classes.root} >
                    <Typography variant="headline" component="h3">
                        This is the result of the game.
                    </Typography>
                    { this.state.ret }
                </Paper>
            </div>
        );
    }
}

export default withStyles(resultStyles)(ResultPanel);


